.report {
 width: 50px;
  height: 50px; 
  background-color: black;
  color: white;
  position:sticky;  /* relative; */
  animation-name: example;
  animation-duration: 4s;
}

@keyframes example {
  0%   {background-color:rgb(90, 178, 216); left:0px; top:0px;}
  25%  {background-color:rgb(17, 100, 182); left:200px; top:0px;}
  50%  {background-color:blue; left:200px; top:200px;}
  75%  {background-color:rgb(47, 6, 117); left:0px; top:200px;}
  100% {background-color:rgb(125, 18, 86); left:0px; top:0px;}
}

